body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #58585a;
}

#gameboard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #f2dda7;
  height: 100vh;
}

#body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#instructions {
  text-align: center;
  margin: 12px;
}

#main-logo {
  text-align: center;
}

#header {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#logo {
  margin: 12px;
}

#expenses {
  text-align: center;
}

#city {
  width: 75%;
  max-height: 500px;
  min-height: 500px;
  background-image: url('/images/little-city.jpg');
  background-repeat: no-repeat;
  background-size: 100%;
  display: -ms-flexbox;
  display: flex;
}

#open {
  border: 3px solid #91278e;
  background-color: #ef59a1;
  color: #fff;
  width: 120px;
  border-radius: 8px;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

#cash {
  margin: 12px;
  margin-right: 100px;
  font-size: 40px;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  color: #53456a;
}

#cash div {
  margin-top: 8px;
}

#number, #cost {
  font-size: 40px;
  margin-left: 12px;
}

#number {
  color: #53456a;
}

#cost {
  color: #99ca3b;
}

#selectors {
  display: -ms-flexbox;
  display: flex;
  width: 90%;
}

.selectorPanel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.selector img {
  max-width: 50px;
  cursor: pointer;
}

.panel {
  background-color: #fff;
  border-radius: 4px;
  width: 81%;
  margin: 12px;
  padding: 12px 25px;
}

.limo {
  height: 29px;
  width: 100px;
  margin: 2px 0;
  position: absolute;
}


